
.producttable-wrapper{
    background: #1f1e25;
    width: 95%;
    /* height: 90vh; */
    display: flex;
    margin: 0% 2.5%;
    box-shadow: 0px 1px 3px #80808069;
    flex-direction: column;
    margin-top: 2%;

}

.product-table-Header{
    width: 100%;
    height: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-bottom: 1px solid #80808080;
}

.table-right{
    width: 55%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 10px;

}

.table-right h2{
    font-family: system-ui;
    font-size: 18px;
    font-weight: 500;
    color: #ffffffb0;

}

.table-left{
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 10px;
}

.btn-blue{
    padding: 10px 11px;
    background: ble;
    background: #fffffffa;
    color: rgb(0, 0, 0);
    border-radius: 3px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2px;
}

.btn-blue span{
    font-size: 14px;
}

.btn-blue  h2{
    font-size: 12px;
    font-family: system-ui;
    font-weight: 500;
}



