
@font-face {
    font-family: Harlow;
    src: url(/public/fonts/Harlow-Solid.ttf);
  }
  
.header-wrapper{
    width: 100%;
    height: 60px;
    background: var(--background-color);
    display: flex;
    flex-direction: row;
    padding-left: 20px;
    position: sticky;
    top: 0;
    z-index: 10;
    
}

.header-logo{
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
}

.header-logo span{
    color: var(--white);
    font-family:Harlow;
    font-size: 21px;
}

/******************Right Navigation********************/

.header-right-nav{
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    gap: 30px;
    padding-right: 27px;
}

.header-right-nav ul{
    list-style: none;
    color: var(--white);
    font-family: Phagspa;
    display: flex;
    gap: 49px;
    font-size: 17px;
}

.header-right-nav ul li{
    cursor: pointer;
    letter-spacing: 1px;
}

.header-right-nav button{
    color: #000000;
    background: white;
    font-family: 'Phagspa';
    padding: 7px 11px;
    font-weight: 900;
    letter-spacing: 3px;
    border-radius: 4px;
    cursor: pointer;

}

.header-right-nav button:hover{
    background: rgba(255, 255, 255, 0.791);
}



