.Pop-Up-Inner-Wrapper{
    width: 85%;
    height: 92%;
    margin: 2% 7%;
    background: var(--background-color);
    border: 1px solid white;
    position: relative;
}

.Pop-Up-Inner-Wrapper > span{
    position: absolute;
    right: 1%;
    top: 1%;
    font-size: 42px;
    color: white;
    cursor: pointer;
}

.Pop-Up-Inner-Wrapper > span:hover{
    color: rgba(255, 255, 255, 0.782);
}

.Media-Wrapper{
    width: 100%;
    height: 85%;
    display: flex;
    flex-direction: row;
    padding: 27px 75px;
    gap: 20px;
    margin: 0px 43px;

}

.Small-Images-Wrapper{
    width: 15%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* background-color: gray; */
    gap: 23px;

}

.small-image-wrapper{
    width: 100%;
    height: 30%;
    border-radius: 5px;
}

.small-image-wrapper img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}


.Large-Images-Wrapper{
    width: 80%;
    height: 100%;
    display: flex;
    background: rgba(9, 8, 8, 0.614);
}

.Large-Images-Wrapper img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.Title-wrapper{
    width: 70%;
    height: 11%;
    display: flex;
    flex-direction: row;
    gap: 20px;
    margin: 0px 25%;
    background: #ffffffa8;
    align-items: center;
    justify-content: flex-start;
    justify-content: center;

}

.Title-wrapper h2{
    font-family: 'Phagspa';
    font-size: 24px;

}

@media (max-width:600px) {
    .Pop-Up-Inner-Wrapper{
        width: 100%;
        height: 100%;
        margin: 0px;
    }

    .Media-Wrapper{
        flex-direction: column-reverse;
        margin: 0px;
        padding: 0px;
        gap: 5px;
    }

    .Small-Images-Wrapper{
        flex-direction: row;
        width: 90%;
        margin: 0% 5%;
        height: 40%;
    }

    .small-image-wrapper{
        height: 70%;
    }

    .Large-Images-Wrapper{
        width: 90%;
        margin: 4% 6%;
    }

    .Title-wrapper{
        width: 90%;
        margin: 0px 5%;
    }
}
