.Sidebar-wrapper{
    width: 17%;
    /* height: calc(100vh - 70px); */
    background: #1f1e25;
    box-shadow: 0px 4px 4px #8080808c;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 1%;
    height: 100vh;
}



.Sidebar-wrapper ul{
    display: flex;
    flex-direction: column;
    list-style: none;
    width: 100%;
    height: 100%;
    font-size: 14px;
    color: #ffffff;
    gap: 6px;
    padding-top: 7px;


}

.Sidebar-wrapper ul li{
    display: flex;
    width: 100%;
    height: 40px;
    align-items: center;
    font-family: system-ui;
    gap: 11px;
    position: relative;
    cursor: pointer;
}

.Sidebar-wrapper ul li::before{
    content: " ";
    width: 0%;
    height: 100%;
    background-color:rgb(42 49 79);
    position: absolute;
    z-index: 0;
    transition: all 0.5s ease;
    cursor: pointer;
    
}

.Sidebar-wrapper ul li:hover::before{
    width: 100%;
}

.listitem{
    width: 70%;
    height: 100%;
    display: flex;
    align-items: center;
    gap: 10px;
    margin: 0% 15%;
    z-index: 1;
    text-decoration: none;
    color: white;
}

