.Model-Show-Section{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    padding: 17px 47px;
    margin-top: 28px;
    gap: 95px;
    flex-wrap: wrap;

}

.Model-Wrapper{
    width: 28%;
    height: 300px;
    background: inherit;
    box-shadow: 0px 1px 6px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    cursor: pointer;
    transition: 0.4s ease;
}

.Model-Wrapper img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.Model-Wrapper-Inner{
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    backdrop-filter: brightness(0.5);
    display: flex;
    align-items: center;
    justify-content: center;
}

.Model-Wrapper:hover{
    transform: translate(0px, -12px);
    transition: 0.4s ease;

}

.Model-Wrapper-Inner span{
    color: white;
    font-size: 39px;
    font-family: 'Phagspa';
    letter-spacing: 3px;
    font-weight: bolder;

}


/*************Media Queries********/

@media (max-width:600px) {

    .Model-Wrapper{
        width: 100%;
    }
    
}

@media (min-width:800px) {
    .Model-Wrapper{
        width: 40%;
    }
    
}

@media (min-width:1000px) {
    .Model-Wrapper{
        width: 28%;
    }
    
}

