
@font-face {
    font-family: Harlow;
    src: url(/public/fonts/Harlow-Solid.ttf);
  }
  

.outer-wrapper-admin{
    width: 100%;
    height: 70px;
    box-shadow: 0px 0px 8px #00000054;
    display: flex;
    flex-direction: row;
    align-items: center;
    box-shadow: 0px 0px 5px #3e3e3e42;
    background: #1f1e25;;
    
}

.header-logo-admin{
    width: 18%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    /* border: 1px solid #8080803d; */
    /* background-color: white; */
}

.header-logo-admin > a{
    text-decoration: none;
    font-family: Harlow;
    font-size: 24px;
    color: rgb(255, 255, 255);
}

.Nav-left-admin{
    width: 52%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 15px;
}

.Nav-left-admin a{
    text-decoration: none;
    padding: 10px;
    font-size: 15px;
    color: rgb(255, 255, 255);
    font-family: system-ui;
    /* transition: all 0.5s ease; */
    outline: none;
    
}

.Nav-right-admin{
    width: 30%;
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: flex-end;
    margin-right: 20px;
}

.Nav-right-admin ul{
    list-style: none;
    display: flex;
    align-items: center;
    flex-direction: row;
    gap: 20px;
}

.Nav-right-admin ul  li{
    font-size: 15px;
    font-family: system-ui;
    color: #ffffffbc;
    font-weight: 400;
    cursor: pointer;
}

.Nav-right-admin ul li a{
    text-decoration: none;
    font-size: 15px;
    font-family: system-ui;
    color: #ffffffbc;
    font-weight: 400;
    cursor: pointer;
}


/********LOGOUT*************/


@media (max-width:700px) {
    .Nav-right-admin{
        display: none;
    }

    .header-logo-admin{
        width: 80%;
        justify-content: flex-start;
        margin-left: 20px;
    }

    .Nav-left-admin{
        display: none;
    }
}

