.Login-Outer-Most-Wrapper{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: row;
    overflow: hidden;
}



.Login-Left-Outer-Wrapper{
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    background-color: #9a7e7e4f;
}


.Login-Right-Outer-Wrapper{
    display: flex;
    width: 50%;
    height: 100%;
    box-shadow: 0px 0px 6px #8080808f;
    flex-direction: column;
    align-items: center;
}

.Login-Main-Wrapper{
    width: 64%;
    height: 50vh;
    margin:24% 18%;
    display: flex;
    flex-direction: column;
    box-shadow: 0px 1px 3px #808080d4;
}

/*********Title*********/

.Login-Title{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 19%;
}

.Login-Title h2{
    font-family: Phagspa;
    font-size: 27px;
    letter-spacing: 5px;
    color: white;
   
    
}




/******Login Form*******/

.Login-Form-Wrapper{
    width: 100%;
    height: 80%;
    display: flex;
    flex-direction: column;
}

.Login-Form-Wrapper form{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 8%;
}

.Login-Input{
    width: 90%;
    margin: 0% 5%;
    height: 25%;
    border: 1px solid #80808059;
    border-radius: 4px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}
.Login-Input span{
    color: #454545cf;
    font-size: 23px;
    font-weight: 200;

}

.Login-Input input{    
    height: 61%;
    border: none;
    background: none;
    font-size: 14px;
    width: 85%;
    outline: none;
    color: white;

}





/**********Register***********/

.Register-Action{
    width: 100%;
    height: 100%;
    background: none;
    border: none;
    outline: none;
    background: #0095ffd1;
    color: white;
    /* font-weight: 500; */
    font-family: 'system-ui';
    font-size: 15px;
    letter-spacing: 2px;

}

.Register-Action:hover{
    cursor: pointer;
    background: #0095fff1;

}


/***************Left-Side-Bar***********/

.Login-Left-Text-Wrapper{
    width: 100%;
    height: 100%;
    display: flex;
    position: relative;

}

.Login-Left-Text{
    width: 73%;
    height: 15%;
    position: absolute;
    top: 40%;
    display: flex;
    left: 86px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Logo-Login{
    width: 100%;
    height: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Logo-Login #unknown{
    font-size: 50px;
    font-family: 'Phagspa';
    letter-spacing: 3px;
    color: white;
    padding: 0px 10px;
    text-shadow: -1px 1px 12px black

}

.Logo-Login #modeler{
    font-size: 50px;
    font-family: 'Phagspa';
    letter-spacing: 3px;
    color: white;
    padding: 0px 10px;
    text-shadow: -1px 1px 12px black;
    z-index: 20;

}

.Tag-Line{
    width: 71%;
    height: 30%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: -4%;
    z-index: 12;
    /* background: #0b0d0e1c; */
    margin-top: 2%;
    border-radius: 30px;
    box-shadow: 1px 1px 7px white;
    color: black;
}

.Tag-Line p{
    font-size: 17px;
    font-family: 'Phagspa';
    letter-spacing: 1px;
    color: white;
    text-shadow: 0px 0px 6px bla

}

.Filter-Photo{
    width: 100%;
    height: 100%;
    backdrop-filter: brightness(0.5);
}

#tree1{
    height: 90%;
    position: absolute;
    top: 13%;
    left: -22%;
}

#tree2{
    height: 90%;
    position: absolute;
    left: 17%;
    bottom: -2%;
    z-index: 11;
}


/**************Media Queries**************/
@media (max-width:700px) {
    .Login-Left-Outer-Wrapper{
        display: none;
    }

    .Login-Right-Outer-Wrapper{
        width: 100%;
    }
    
}


@media (max-width:700px) {
    .Login-Left-Outer-Wrapper{
        display: none;
    }

}

@media (max-width:1100px) {
    .Login-Main-Wrapper{
        width: 70%;
    }
    
}


@media (max-width:450px) {
    .Login-Main-Wrapper{
       width: 94%;
       height: 91%;
}
      
    .Login-Form-Wrapper form{
        height: 45%;
    }
    
    
}