.Model-Section-Wrapper{
    width: 100%;
    min-height: 100vh;
    display: flex;
    background: var(--hero-background);
    padding: 19px 51px 0px 40px;
    gap: 50px;
    justify-content: flex-start;
    flex-wrap: wrap;


}

.Single-Model-Wrapper{
  
    width: 22%;
    height: 30vh;
    box-shadow: -2px 4px 5px #1716165e;
    cursor: pointer;
    position: relative;
    border: 2px solid white;
    

}

.Image-wrapper{
    width: 100%;
    height: 100%;
    display: flex;
    background: #80808057;

}

.Image-wrapper img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.hover-display-wrapper{
    opacity: 0;  
    transition: 0.3s;
    /* pointer-events: none;  */
    position: absolute;
    top: 0;
    width: 100%;
    height: 0%;
    position: absolute;

}



.Single-Model-Wrapper:hover .hover-display-wrapper{
    opacity: 1;  
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #080808ab;
    backdrop-filter: blur(1px);
    gap: 10px;
    transition: all 0.3s ease;
}

.hover-display-wrapper span{
    font-size: 21px;
    font-family: 'Phagspa';
    color: var(--white);
    font-weight: lighter;
}

.hover-display-wrapper button{
    background: black;
    color: white;
    padding: 7px 15px;
    border-radius: 31px;
    letter-spacing: 2px;
    cursor: pointer;
    border: none;
    outline: none;
}

.hover-display-wrapper button:hover{
    background: rgba(0, 0, 0, 0.621);
}

.Pop-Up-Wrapper{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    background: #2f2f2f91;
    z-index: 100;
    backdrop-filter: blur(2px);
    opacity: 1;
    transition: 0.5s;
    left: 0;

}

.Pop-Up-Wrapper-Hide{
    pointer-events: none;
    opacity: 0;
    transform: scale(0);
    transition: 0.5s;
}

@media (max-width:700px) {
    .Single-Model-Wrapper{
        width: 90%;
        /* height: 200px; */
        height: 30vh;
    }
}



@media (min-width:800px) {
    .Single-Model-Wrapper{
        width: 30%;
        height: 50vh;
    }
}


@media (min-width:1300px) {
    .Single-Model-Wrapper{
        width: 22%;
        height: 38vh;
    }
}
