
:root{
  --white: #ffffff;
  --black: #000000;
  --background-color:#1c1c1f;
  /* --background-color:#28282b; */
  --hero-background:#28282b;
  /* --hero-background:#1c1c1f; */
  
}

*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body{
  background:var(--background-color);
  min-height: 100vh;
  overflow-x: hidden;

}

@font-face {
  font-family: Phagspa;
  src: url(/public/fonts/phagspa.ttf);
}

@font-face {
  font-family: Phagspab;
  src: url(/public/fonts/phagspab.ttf);
}

@font-face {
  font-family: Harlow;
  src: url(/public/fonts/Harlow-Solid.ttf);
}



