.hero-wrapper{
    width: 100%;
    height: 500px;
    display: flex;
    flex-direction: row;
    padding-left: 40px;
    background: var(--hero-background);

}

.hero-left-wrapper{
    width: 40%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    overflow-x: hidden;
}

#Large-Hero-Text{
    font-family: Harlow;
    color: var(--white);    
    font-size: 67px;
    font-weight: 400;
    letter-spacing: 2px;
    position: relative;
}

.hero-wrapper:hover #Large-Hero-Text::after{
    content: " ";
    height: 100%;
    width: 100%;
    position: absolute;
    white-space: nowrap;
    left: 3px;
    background: var(--hero-background);
    animation: typing 2s steps(30,end) infinite ;
    border-left: 2px solid red;

}

@keyframes typing {

    from{
        left: 0%;
    }
    to{
        left: 100%;
        margin: 0 -35px 0 35px;

    }
    
}

#Small-Hero-Text{
    font-family: 'Phagspa';
    color: var(--white);
    font-size: 24px;
    letter-spacing: 18px;
    margin-left: 57px;
    margin-top: 15px;
    text-shadow: 4px 5px 5px #000000;
    position: relative;

}

#experience{
    font-size: 25px;
    font-family: 'Harlow';
    color: var(--white);
    margin-top: 29px;
    letter-spacing: 2px;
    margin-left: 11px;

}


.hero-left-wrapper button{
    margin-top: 26px;
    padding: 11px 14px;
    background: #713333;
    border: none;
    color: white;
    border-radius: 4px;
    font-family: 'Phagspa';
    box-shadow: 0px 4px 8px #0000005c;
    cursor: pointer;
}

.hero-left-wrapper button:hover{
    background: #9d2d2d;

}


/***************Hero Right*************/

.hero-right-wrapper{
    width: 60%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
}

.hero-wrapper img {
    width: 98%;
    height: 100%;
    object-fit: contain;
}





/***********Media Queries**********/


@media (min-width:1000px) {
    #Large-Hero-Text{
        font-size: 55px;
    }

    #Small-Hero-Text{
        font-size: 19px;
        margin-left: 37px
    }

    
}

@media (min-width:1200px) {
    #Large-Hero-Text{
        font-size: 67px;
    }

    #Small-Hero-Text{
        font-size: 24px;
        margin-left: 57px
    }
}



@media (max-width:900px) {
   

    .hero-right-wrapper img{
        width: 120%;
    }
    
    #Large-Hero-Text{
        font-size: 48px;
    }

    #Small-Hero-Text{
        font-size: 12px;
        margin-left: 36px;

    }

    .hero-left-wrapper{
        width: 60%;
    }

    #experience {
        font-size: 19px;
    }
    .hero-wrapper:hover #Large-Hero-Text::after{
         margin-top: none;
        padding:none;
        background:none;
        border: none;
        color:none;
        border-radius:none;
        font-family: none;
        box-shadow: none;
    }
    


    
}

@media (max-width:500px) {
    .hero-left-wrapper{
     width: 99%;
     z-index: 5;
     /* position: absolute; */
     backdrop-filter: blur(2px);
     padding-left: 44px;
     background: #151314d4;

    }

    .hero-right-wrapper{
      z-index: 0;
      position: absolute;
      bottom: -69px;
      left: 129px;
    }

    .hero-right-wrapper img{
        width: 140%;
    }

    .hero-wrapper{
        width: 100%;
        padding: 0;
    }

    #Large-Hero-Text{
        background: #000000f0;
        padding: 0px 11px;
        overflow: hidden;
        font-size: 45px;
    }

    #Small-Hero-Text{
        margin-left: 17px;
    }

